@import url('https://fonts.googleapis.com/css2?family=Inter&family=Libre+Caslon+Display&family=Marcellus&display=swap');

* {
  font-family: 'Marcellus', serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
}

ul,
li {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
  display: inline-block;
}

a:hover {
  text-decoration: none;
  color: inherit;
}

p {
  margin: 0;
  padding: 0;
}

.btn {
  box-sizing: border-box;
  background: #FBDBA3;
  opacity: 0.8;
  border: 1px solid #FBDBA3;
  border-radius: 15px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.25;
  color: #000000;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
}

.btn:hover {
  background: #333333;
  color: #ffffff;
  border: 1px solid #FBDBA3;
  transition: all 0.5s ease-in-out;
}

.btn:disabled {
  background: transparent;
  color: #8d8c8c;
  border: 1px solid #FBDBA3;
  transition: all 0.5s ease-in-out;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus {
  border: 1px solid #FBDBA3;
  -webkit-text-fill-color: #FFFFFF;
  -webkit-box-shadow: 0 0 0px 1000px #424141 inset;
  transition: background-color 5000s ease-in-out 0s;
}

.form__input {
  max-height: 57px;
  border: 1px solid #FBDBA3;
  border-radius: 15px;
  background-color: transparent;
  padding: 4%;
  color: #FFFFFF;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.25;
  caret-color: #FFFFFF;
}

.form__input:focus-visible {
  outline: none;
}

.form__input::placeholder {
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.25;
  color: #FFFFFF;
}
